body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: white;
  font-family: 'Montserrat', sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* header */
.header{
  width: 100%;
  height: 90px;
  /*background-color: #32729C;*/
  background-color: #0b629cb2;
  backdrop-filter: blur(5px);
  display: flex;
  flex-direction: row;
  align-items: center;
  z-index: 1;
}
.logo-header{
  width: 20%;
  height: 100%;
  background-repeat: no-repeat;
  background-position-x: center;
  background-position-y: center;
  z-index: 1;
}
.logo{
  width: 60px;
  height: 60px;
  margin-right: 100px;
}
.btns{
  width: 80%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.ul-header{
  display: flex;
}
li{
  list-style: none;
}
li:nth-child(6){
  border: 2px solid #F6FF00;
  border-radius: 5px;
  padding: 10px;
}
.more{
  display: none;
  margin-right: 20px;
  z-index: 1;
}



/* landing */
.landing{
  width: 100%;
  height: 100vh;
  position: relative;
  transition: all .5s linear;
  background-position-x: center;
  background-position-y: center;
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: left;
  
}
.content-landing{
  width: 765px;
  height: 200px;
  background-color: #0b629cb2;
  margin-left: 30px;
  display: flex;
  justify-content: space-between;
  padding: 40px;
}
.smallfont{
  font-size: 15px;
  text-transform: uppercase;
}
.bigfont{
  font-size: 40px;
  text-transform: uppercase;
}
.btn1{
  width: 120px;
  height: 20px;
  background-color: #F6FF00;
  align-self: flex-end;
  padding: 10px;
  color: #0b629c;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 10px;
}

/* half landing */
.half{
  height: 100vh;
  background-position-x: center;
  background-position-y: center;
  background-size: cover;
  position: relative;
}
.over{
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #18649bc5;
  
}
.building{
  z-index: 1;
  position: absolute;
  bottom: 0;
  right: 150px;
  width: 250px;
  height: 600px;
}
.half-talk{
  color: white;
  position: absolute;
  left: 100px;
  top: 240px;
  width: 60%;
  height: 250px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-content: left;
}
.half-talk div:nth-child(1){
  font-size: 35px;
  font-weight: bold;
}
.half-talk div:nth-child(2),.half-talk div:nth-child(3){
  width: 700px;
}


/* services */
.services{
  width: 100%;
  height: 75vh;
  
}
.items{
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: left;
  align-self: center;
}
.item{
  width: 20%;
  height: 600px;
  position: relative;
}
.item p{
  position: absolute;
  bottom: 5px;
  left: 20px;
  z-index: 1;
  font-size: 20px;
}
.over-blue{
  position: absolute;
  width: 100%;
  height: 100%;
  bottom: 0;
  background: linear-gradient(
        180deg,
        #da22ff00,
        #32729c8e
    );

}