@media only screen and (max-width : 575px) {
    .header{
        position: relative;
    }

    .content-landing{
        width: 100%;
        flex-direction: column;
        margin-left: 0px;
    }
    .smallfont{
        font-size: 10px;
    }
    .bigfont{
        font-size: 30px;
    }
    .btn1{
        align-self: center;
    }
    .half-talk{
        top: 100px;
        left: 40px;
        height: 70%;
        width: 78%;
    }
    .half-talk div:nth-child(2),.half-talk div:nth-child(3){
        width: 300px;
    }
}
@media only screen and (min-width : 575px) and (max-width : 768px) {
    .half-talk{
        top: 200px;
        left: 50px;
        height: 60%;
        width: 78%;
        font-size: 25px;
    }
    .half-talk div:nth-child(2),.half-talk div:nth-child(3){
        width: 600px;
    }
}
@media only screen and (max-width : 776px) {
    .btns{
        display: none;
        position: absolute;
        top: 0;
        left: 0;
        height: 100vh;
        background-color: #32729cc2;
        backdrop-filter: blur(5px);
        display: flex;
        justify-content: center;
        align-self: center;
        width: 100%;
    }
    .btns ul{
        flex-direction: column;
        width: 200px;
        height: 80%;
        justify-content: space-evenly;
        align-items: center;
    }
    .more{
        display: block;
    }
    .header{
        justify-content: space-between;
    }
    .building{
        display: none;
    }
    
}
@media only screen and (min-width : 777px){
    .btns{
        width: 80%;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .ul-header{
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        width: 800px;
    }
}
@media only screen and (min-width : 576px) {
    .header{
        position: fixed;
    }
}